import React from 'react';
import { Grid, Typography, Link, TextField } from '@mui/material';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook, faTwitter, faInstagram, faLinkedin } from '@fortawesome/free-brands-svg-icons';

function Footer() {
    return (
        <footer style={{ backgroundColor: '#33485E', padding: '20px', marginTop: 'auto' }}>
            <Grid container justifyContent="space-between" alignItems="center">
                <Grid item xs={12} md={4}>
                    <Typography variant="h6" style={{ color: 'white', marginBottom: '10px' }}>
                        Contact Us
                    </Typography>
                    <Typography variant="body1" style={{ color: 'white' }}>
                        Email: example@example.com
                    </Typography>
                    <Typography variant="body1" style={{ color: 'white' }}>
                        Phone: +1234567890
                    </Typography>
                    <TextField label="Send us a message" variant="filled" fullWidth style={{ marginTop: '10px' }} />
                </Grid>
                <Grid item xs={12} md={4}>
                    <Typography variant="h6" style={{ color: 'white', marginBottom: '10px' }}>
                        Follow Us
                    </Typography>
                    <Link href="#" color="inherit" style={{ marginRight: '10px' }}>
                        <FontAwesomeIcon icon={faFacebook} size="lg" />
                    </Link>
                    <Link href="#" color="inherit" style={{ marginRight: '10px' }}>
                        <FontAwesomeIcon icon={faTwitter} size="lg" />
                    </Link>
                    <Link href="#" color="inherit" style={{ marginRight: '10px' }}>
                        <FontAwesomeIcon icon={faInstagram} size="lg" />
                    </Link>
                    <Link href="https://www.linkedin.com/in/e-cell-lnjpit-chapra-464a21287" color="inherit">
                        <FontAwesomeIcon icon={faLinkedin} size="lg" />
                    </Link>
                </Grid>
            </Grid>
            <Typography variant="body2" style={{ color: 'white', textAlign: 'center', marginTop: '20px' }}>
                &copy; 2024 E-Cell
            </Typography>
        </footer>
    );
}

export default Footer;