import React, { useEffect } from 'react';
import './Home.css'; // Create this CSS file for styling
import { Grid, Box, Typography, Container } from '@mui/material';
import Footer from '../components/Footer';
import { makeStyles } from '@mui/styles';
import TestimonialContainer from '../components/TestimonialContainer'; // Import the TestimonialContainer component
import Banner from './Banner';
import logo from '../assets/img/header-img.svg';
import LogoSVG from '../components/LogoSVG'; // Import the LogoSVG component

// Define custom CSS styles
const useStyles = makeStyles((theme) => ({
    headingText: {
        background: '-webkit-linear-gradient(45deg, red 30%, yellow 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
    },
    nomalHeading: {
        background: '-webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
    },

    container: {
        position: 'relative', // Ensure relative positioning for the container
        alignItems: 'center',
        justifyContent: 'center',
        minHeight: '100vh',
        paddingTop: '200px'
    },



}));
function Home() {

    const classes = useStyles();

    const initiatives = [
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Outreach Program', details: 'Facilitate student engagement with Startup Bihar Team and Department of Industry through the Outreach Program. Identify and reward 5 students with innovative ideas with certificates and gifts, fostering a culture of entrepreneurship and innovation among the youth in Bihar.' },
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Initiative 2', details: 'It seemed like it should have been so simple. There was nothing inherently difficult with getting the project done.' },
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Initiative 3', details: 'It seemed like it should have been so simple. There was nothing inherently difficult with getting the project done.' },
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Initiative 4', details: 'It seemed like it should have been so simple. There was nothing inherently difficult with getting the project done.' },
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Initiative 5', details: 'It seemed like it should have been so simple. There was nothing inherently difficult with getting the project done.' },
        { logo: '/images/Ecell-logos/nature1.jpg', title: 'Initiative 6', details: 'It seemed like it should have been so simple. There was nothing inherently difficult with getting the project done.' }
    ];


    // Testimonials data
    const testimonials = [
        { name: 'Dr. Mithlesh Kumar Singh', position: 'Principal, LNJPIT Chapra', image: '/images/Members/Mithilesh-Singh.jpg', quote: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.' },
        { name: 'Dr. Umesh Chaudhary', position: 'Faculty In-Charge, Startup Cell', image: '/images/Members/Umesh-Chaudhary.jpg', quote: 'As the Faculty In-Charge of the Startup Cell at LNJPIT Chapra, I am proud to witness the transformative impact of our initiatives on budding entrepreneurs. Our commitment to nurturing innovation and providing comprehensive support has been instrumental in empowering aspiring startups to thrive and succeed.' },
        { name: 'Mrs. Lovely Singh', position: 'Startup Coordinator', image: '/images/Members/Mithilesh-Singh.jpg', quote: 'I am impressed by the dynamic approach of the Startup Cell at LNJPIT Chapra and rank holder effort in Bihar Startup Cell Community. Their commitment to nurturing startups cultivates a culture of innovation and propels the region towards prosperity' },
        { name: 'Miss. Surbhi Singh', position: 'Startup Coordinator', image: '/images/Members/Surbhi-Singh.jpg', quote: "As LNJPIT Chapra's Startup Coordinator, I'm passionate about empowering entrepreneurs. Our support fosters innovation and success, driving positive change." }
    ];




    useEffect(() => {
        const scrollContainer = document.getElementById('autoScrollContainer');
        const scrollContent = document.getElementById('autoScrollContent');

        const scrollHeight = scrollContainer.clientHeight;
        const contentHeight = scrollContent.clientHeight;

        if (contentHeight > scrollHeight) {
            const clone = scrollContent.cloneNode(true);
            scrollContainer.appendChild(clone);
        }

        const animateScroll = () => {
            scrollContainer.scrollTop += 0.5; // Slow down the scrolling speed
            if (scrollContainer.scrollTop >= contentHeight) {
                scrollContainer.scrollTop = 0;
            }
        };

        const animationInterval = setInterval(animateScroll, 50);

        return () => {
            clearInterval(animationInterval);
        };
    }, []);
    return (
        <div className='body2'>
            <Container className={classes.container}>
                <Container style={{ padding: '0' }}>
                    <LogoSVG logo={logo} />
                </Container>
                <Banner head={"Welcome to Startup-Cell"} text={"Startup-Cell LNJPIT Chapra is a dynamic hub for budding entrepreneurs and young professionals, fostering innovation and collaboration through workshops, competitions, and mentorship."} />
            </Container>
            <div style={{ padding: '50px' }}>
                <Grid container lg={12} direction="row">
                    <Grid item lg={4} md={4} sm={12} style={{ overflow: 'hidden', position: 'relative', borderRadius: '10px', backgroundColor: 'rgb(51, 72, 94)', height: 'auto', marginRight: '10px', marginTop: '50px' }}>
                        <div style={{ backgroundColor: '#90565656', padding: '10px' }}>
                            <Typography variant="h6" style={{ fontFamily: "Oswald", fontWeight: 'bold', fontSize: '25px', color: 'orange' }}>
                                About Startup-Cell
                            </Typography>
                        </div>
                        <Box id="autoScrollContainer" className="auto-scroll-container">
                            <div id="autoScrollContent" className="auto-scroll-content">
                                <Typography variant="h6" style={{ fontFamily: "Oswald", fontSize: '20px', fontWeight: 'normal',color:'white', textAlign: 'left' }}>Startup-Cell LNJPIT Chapra helps the hustling startups and young professionals
                                    via dynamic workshops, thought-provoking speaker sessions, high-stakes
                                    business plan competitions, and numerous other game-changing initiatives
                                    throughout the year to create a crucible for innovation. We stand as pillars
                                    of support for budding entrepreneurs, providing them with personalized
                                    guidance from experienced mentors, crucial funding opportunities, and a
                                    robust network that can change the course of their journey forever!</Typography>
                            </div>
                        </Box>
                    </Grid>
                    <Grid item lg={3} md={3} sm={12} style={{ marginRight: '10px', marginTop: '50px' }} />
                    <Grid item lg={4} md={4} sm={12} style={{borderRadius: '10px', backgroundColor: 'rgb(51, 72, 94)', marginTop: '50px' }}>
                        <Typography variant="h6" style={{ fontFamily: "Oswald", fontWeight: 'bold', fontSize: '25px', color: 'orange' }}>
                            Notice
                        </Typography>
                    </Grid>
                </Grid>
            </div>

            <Typography className={classes.headingText} style={{
                    fontFamily: 'Oswald,sans-serif',
                    fontWeight: 'bold',
                    marginTop:'100px'
                }} variant='h3'>OUR INITIATIVES</Typography>
            <Grid marginTop={10} container justifyContent="center">
                <Grid container lg={9} spacing={4} justifyContent="center">
                    {initiatives.map((member, index) => (
                        <Grid key={index} item lg={4}>
                            <div class="box">
                                <div class="outer-border">
                                    <div class="inner-border">
                                        <img src={member.logo} alt='' style={{ width: '60px', height: '60px', borderRadius: '10px', marginTop: '20px' }} ></img>
                                        <h4 style={{ color: 'gold' }}>{member.title}</h4>
                                        <p style={{ color: 'white', fontSize: '15px' }}>{member.details}</p>
                                    </div>
                                </div>
                            </div>
                        </Grid>
                    ))}
                </Grid>
            </Grid>


            <Grid container lg={12} marginTop={20} marginBottom={10} justifyContent="center">
                <Grid item lg={5} md={6} sm={8}>
                    <TestimonialContainer testimonials={testimonials} />
                </Grid>
            </Grid>
            <Footer />
        </div>
    );
}

export default Home;
