import React, { useState, useEffect, useMemo } from 'react';
import { Col } from 'react-bootstrap';
import { Container } from '@mui/system';
import { Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';

// Define custom CSS styles
const useStyles = makeStyles((theme) => ({
    HeadText: {
        background: '-webkit-linear-gradient(45deg, red 30%, yellow 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
        animation: '$gradientAnimation 5s linear infinite',
        [theme.breakpoints.down('sm')]: {
            fontSize: '48px', // Adjust the font size for small screens
        },
        [theme.breakpoints.between('sm', 'md')]: {
            fontSize: '50px', // Adjust the font size for medium screens
        },
        [theme.breakpoints.up('lg')]: {
            fontSize: '60px', // Adjust the font size for large screens
        },
    },
    BodyText: {
        background: '-webkit-linear-gradient(45deg, aqua 30%, yellow 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
        animation: '$gradientAnimation 5s linear infinite',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px', // Adjust the font size for small screens
        },
        [theme.breakpoints.between('sm', 'md')]: {
            fontSize: '20px', // Adjust the font size for medium screens
        },
        [theme.breakpoints.up('lg')]: {
            fontSize: '25px', // Adjust the font size for large screens
        },
    },
    '@keyframes gradientAnimation': {
        '0%': {
            backgroundPosition: '0% 50%',
        },
        '50%': {
            backgroundPosition: '100% 50%',
        },
        '100%': {
            backgroundPosition: '0% 50%',
        },
    }
}));

function Banner(data) {
    const classes = useStyles();
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(100); // Adjust this value for smoother animation
    const period = 10;
    
    const toRotate = useMemo(() => [`${data.text}`], [data]);

    useEffect(() => {
        const tick = () => {
            let i = loopNum % toRotate.length;
            let fullText = toRotate[i];
            let updateText;
            if (isDeleting) {
                updateText = fullText.substring(0, text.length - 1);
            } else {
                updateText = fullText.substring(0, text.length + 1);
            }
            setText(updateText);

            if (!isDeleting && updateText === fullText) {
                setTimeout(() => {
                    setIsDeleting(true);
                    setDelta(period);
                }, 4000); // Wait for 2 seconds before starting deletion
            } else if (isDeleting && updateText === '') {
                setIsDeleting(false);
                setLoopNum(loopNum + 1);
                setDelta(100);
            }
        };

        let ticker = setInterval(() => {
            tick();
        }, delta);

        return () => { clearInterval(ticker) };
    }, [text, loopNum, toRotate, isDeleting, delta, period]);

    return (
        <Container style={{padding:'5%'}}>
            <Col xs={12} md={6} lg={7} xl={7}>
                <Typography style={{
                    fontFamily: 'Oswald,sans-serif',
                    fontWeight: 'bold',
                }} variant='h2' className={classes.HeadText} >{data.head}</Typography>
                <Typography className={classes.BodyText} variant='h5' style={{
                    fontFamily: 'Oswald,sans-serif',
                    marginTop:'50px',
                    fontWeight: 'bold',
                    color:'white'
                }} >{text}</Typography>
            </Col>
        </Container>
    );
}

export default Banner;