import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';
import RegistrationForm from './RegistrationForm'; // Assuming you have a RegistrationForm component

import './Header.css';

const Header = () => {
    const [menuOpen, setMenuOpen] = useState(false);
    const [openDialog, setOpenDialog] = useState(false);

    const toggleMenu = () => {
        setMenuOpen(!menuOpen);
    };

    const handleDialogOpen = () => {
        setOpenDialog(true);
    };

    const handleDialogClose = () => {
        setOpenDialog(false);
    };

    const [scrolling, setScrolling] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 100) {
                setScrolling(true);
            } else {
                setScrolling(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const headerClass = scrolling ? 'header scrolled' : 'header';

    return (
        <header className={headerClass}>
            <div className="container">
                <div className="logo">
                    <img src="/images/Ecell-logos/logo_e-cell.jpg" alt="Startup-Cell Logo"  />
                </div>
                <nav className={`nav ${menuOpen ? 'show' : 'hide'}`}>
                    <ul>
                        <li>
                            <Link to="/">HOME</Link>
                        </li>
                        <li>
                            <Link to="/about">ABOUT US</Link>
                        </li>
                        <li>
                            <Link to="/initiative">INITIATIVES </Link>
                            {/* <ul>
                                <li><Link to="/initiative">Initiative 1</Link></li>
                                <li><Link to="/initiative">Initiative 2</Link></li>
                                <li><Link to="/initiative">Initiative 3</Link></li>
                                <li><Link to="/initiative">Initiative 4</Link></li>
                                <li><Link to="/initiative">Initiative 5</Link></li>
                                <li><Link to="/initiative">Initiative 6</Link></li>
                            </ul> */}
                        </li>
                        <li>
                            <Link to="/gallery">GALLERY</Link>
                        </li>
                        <li>
                            <Link to="/contact">CONTACT</Link>
                        </li>
                        <li>
                            <Link to="/blog">BLOGS</Link>
                        </li>
                        <li>
                            <Link onClick={handleDialogOpen}>JOIN US</Link>
                        </li>
                        {/* Add more menu items as needed */}
                    </ul>
                </nav>
                <div className={`menu-icon ${menuOpen ? 'open' : ''}`} onClick={toggleMenu}>
                    <div className="line"></div>
                    <div className="line"></div>
                    <div className="line"></div>
                </div>
            </div>
            <Dialog open={openDialog} onClose={handleDialogClose}>
                <DialogTitle>Registration Form</DialogTitle>
                <DialogContent>
                    <RegistrationForm />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose}>Cancel</Button>
                </DialogActions>
            </Dialog>
        </header>
    );
};

export default Header;
