import React, { useState } from 'react';
import Cropper from 'react-easy-crop';
import { Container, Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid } from '@mui/material';
import getCroppedImg from './cropImage';

const ImageCropper = ({ image, onSelect, onCancel, open  }) => {
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [rotation, setRotation] = useState(0);
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);

  const dialogStyle = {
    minWidth: '50vw', // Adjust the width as needed
    minHeight: '50vh', // Adjust the height as needed
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  };

  const onCropComplete = (croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }

  const showCroppedImage = async () => {
    try {

        getCroppedImg(image, croppedAreaPixels,rotation)
        .then((croppedImageBlob) => {
            onSelect(croppedImageBlob);
        })
        .catch((error) => {
            // Handle any errors
            console.error('Error cropping image:', error);
        });
      
    } catch (e) {
      console.error(e);
    }
  }

  const onSuccess = () => {
    showCroppedImage();
  }



  return (
    <React.Fragment>
      
      <Dialog open={open} onClose={onCancel} PaperProps={{ style: dialogStyle }}>
        <DialogTitle>Crop Image</DialogTitle>
        <DialogContent>
          <Container>
            <Grid container spacing={2} justifyContent="center">
              <Grid item xs={12} md={6}>
                <Cropper
                  image={image}
                  crop={crop}
                  rotation={rotation}
                  zoom={zoom}
                  aspect={4 / 4}
                  onCropChange={setCrop}
                  onRotationChange={setRotation}
                  onCropComplete={onCropComplete}
                  onZoomChange={setZoom}
                />
              </Grid>
            </Grid>
          </Container>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={onSuccess} color="success">
            Select
          </Button>
          <Button variant="contained" onClick={onCancel} color="error">
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
};

export default ImageCropper;
