import React, { useState, useEffect } from 'react';
import { Container, Typography, Grid, Card, CardContent, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from '@mui/material';
import Footer from '../components/Footer';

// Sample static blog data
const sampleBlogs = [
    {
        id: 1,
        title: 'Blog Title 1',
        content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.',
        author: 'John Doe',
        date: 'April 12, 2024',
        backgroundImage: '/images/Backgrounds/blacktheme.jpg', // Example background image URL
    },
    {
        id: 2,
        title: 'Blog Title 2',
        content: 'Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
        author: 'Jane Smith',
        date: 'April 13, 2024',
        backgroundImage: '/images/Backgrounds/blacktheme.jpg', // Example background image URL
    },

    {
        id: 3,
        title: 'Blog Title 3',
        content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.',
        author: 'John Doe',
        date: 'April 12, 2024',
        backgroundImage: '/images/Backgrounds/blacktheme.jpg', // Example background image URL
    },
    {
        id: 4,
        title: 'Blog Title 4',
        content: 'Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
        author: 'Jane Smith',
        date: 'April 13, 2024',
        backgroundImage: '/images/Backgrounds/blacktheme.jpg', // Example background image URL
    },
    // Add more blog data as needed
];

function BlogPage() {
    const [blogs, setBlogs] = useState([]);
    const [selectedBlog, setSelectedBlog] = useState(null);
    const [dialogOpen, setDialogOpen] = useState(false);

    useEffect(() => {
        // Fetch blogs from backend or admin page
        // For now, using sampleBlogs as static data
        setBlogs(sampleBlogs);
    }, []);

    const handleCardClick = (blog) => {
        setSelectedBlog(blog);
        setDialogOpen(true);
    };

    const handleCloseDialog = () => {
        setSelectedBlog(null);
        setDialogOpen(false);
    };

    return (

        <div style={{ width: '100%',  display: 'flex', flexDirection: 'column', minHeight: '100vh', backgroundImage:`url('/images/Backgrounds/blueSky1.jpg')`,backgroundRepeat:'no-repeat',backgroundSize:'cover',backgroundAttachment:'fixed',backgroundPosition:'center',height:'auto'}}>
            <div style={{minHeight:'100vh'}}>
            </div>
            <Container maxWidth="lg" style={{paddingBottom:'200px'}}>
                <Grid container spacing={3}>
                    {blogs.map((blog) => (
                        <Grid item key={blog.id} xs={12} md={6}>
                            <Card style={{ height: '300px', backgroundImage: `url(${blog.backgroundImage})`, backgroundSize: 'cover', backgroundPosition: 'center', cursor: 'pointer' }} onClick={() => handleCardClick(blog)}>
                                <CardContent>
                                    <Typography variant="h5" component="h2" gutterBottom style={{ color: 'white' }}>{blog.title}</Typography>
                                    <Typography variant="body1" color="text.secondary" style={{ color: 'white' }}>{blog.content}</Typography>
                                    <Typography variant="subtitle2" color="text.secondary" style={{ color: 'white' }}>Author: {blog.author}</Typography>
                                    <Typography variant="subtitle2" color="text.secondary" style={{ color: 'white' }}>Date: {blog.date}</Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
            <Dialog open={dialogOpen} onClose={handleCloseDialog}>
                <DialogTitle>{selectedBlog?.title}</DialogTitle>
                <DialogContent>
                    <DialogContentText>{selectedBlog?.content}</DialogContentText>
                    <Typography variant="subtitle2">Author: {selectedBlog?.author}</Typography>
                    <Typography variant="subtitle2">Date: {selectedBlog?.date}</Typography>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseDialog} color="primary">Close</Button>
                </DialogActions>
            </Dialog>
            <Footer/>
        </div>

    );
}

export default BlogPage;
