import { React, useEffect, useState } from 'react'
import '../styles/ContactPage.css'
import '@fortawesome/fontawesome-free/css/all.css';
import { Grid, Typography } from '@mui/material';
import Footer from '../components/Footer'

function ContactPage() {
    const [Head, setHead] = useState([]);
    const [OHead, setOHead] = useState([]);
    const [SR, setSR] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const formDataToSend = new FormData();
                formDataToSend.append('Token', 12345678);

                const response = await fetch('https://agamyatech.in/getMembers', {
                    method: 'GET'
                });

                if (response.ok) {
                    const responseData = await response.json();
                    if (responseData && responseData.status === "Success") {
                        setHead(responseData.HeadData || []);
                        setOHead(responseData.OverallHeadData || []);
                        setSR(responseData.SRData || []);
                    } else {
                        alert('Registration failed. Please try again later.');
                    }
                } else {
                    throw new Error('Failed to submit form');
                }
            } catch (error) {
                console.error('Error:', error);
            }
        };

        fetchData();
    }, []);

    return (
        <div className='body' style={{ display: 'flex', flexDirection: 'column', minHeight: '100vh'}}>
            <section className='section1'>
                <div className='section1-content'>
                    <Typography variant='h2' style={{fontFamily:'Oswald,sans-serif',  fontWeight:'bold', color:'orange'}}>GET IN TOUCH WITH US</Typography>
                    <Typography variant='h5' style={{fontFamily:'Oswald,sans-serif', color:'orange'}}>We're here to help and answer any questions you may have</Typography>
                </div>
            </section>


            <div className='section2-content'>
                {OHead.length > 0 && (
                    <Grid container justifyContent="center">
                        <h1 style={{color:'white'}}>Overall Head</h1>
                        <Grid container lg={12} justifyContent="center">
                            {OHead.map((member, index) => (
                                <Grid key={index} item sx={{ marginTop: 4 }} justifyContent="center">
                                    <div className='profile-card'>
                                        <div className='img'>
                                            <img src={"https://back1.agamyatech.in/" + member.ProfileImg} alt={member.Name} />
                                        </div>
                                        <div className='caption'>
                                            <h3 style={{ fontSize: '16px' }}>{member.Name}</h3>
                                            <p>{member.Post}</p>
                                            <div className='social-links'>
                                                <a href={`mailto:${member.Email}`}><i className='fas fa-envelope'></i></a>
                                                <a href={member.LinkedIn} target='_blank' rel='noopener noreferrer'><i className='fab fa-linkedin'></i></a>
                                            </div>
                                        </div>
                                    </div>
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                )}

                {SR.length > 0 && (
                    <Grid container justifyContent="center">
                        <h1 style={{ marginTop: '100px' , color:'white'}}>Overall Co-Ordinator</h1>
                        <Grid container lg={12} justifyContent="center">
                            {SR.map((member, index) => (
                                <Grid key={index} item sx={{ marginTop: 4 }}>
                                    <div className='profile-card'>
                                        <div className='img'>
                                            <img src={"https://back1.agamyatech.in/" + member.ProfileImg} alt={member.Name} />
                                        </div>
                                        <div className='caption'>
                                            <h3>{member.Name}</h3>
                                            <p>{member.Post}</p>
                                            <div className='social-links'>
                                                <a href={`mailto:${member.Email}`}><i className='fas fa-envelope'></i></a>
                                                <a href={member.LinkedIn} target='_blank' rel='noopener noreferrer'><i className='fab fa-linkedin'></i></a>
                                            </div>
                                        </div>
                                    </div>
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                )}

                {Head.length > 0 && (
                    <Grid container justifyContent="center">
                        <h1 style={{ marginTop: '100px', color:'white' }}>Startup-Cell Core Team</h1>
                        <Grid container lg={12} justifyContent="center">
                            {Head.map((member, index) => (
                                <Grid key={index} item sx={{ marginTop: 4 }}>
                                    <div className='profile-card'>
                                        <div className='img'>
                                            <img src={"https://back1.agamyatech.in/" + member.ProfileImg} alt={member.Name} />
                                        </div>
                                        <div className='caption'>
                                            <h3>{member.Name}</h3>
                                            <p>{member.Post}</p>
                                            <div className='social-links'>
                                                <a href={`mailto:${member.Email}`}><i className='fas fa-envelope'></i></a>
                                                <a href={member.LinkedIn} target='_blank' rel='noopener noreferrer'><i className='fab fa-linkedin'></i></a>
                                            </div>
                                        </div>
                                    </div>
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                )}
            </div>
            <Footer />
        </div>
    )
}

export default ContactPage