import React, { useState, useEffect } from 'react';
import { Box, Typography, Avatar, Paper } from '@mui/material';

const TestimonialContainer = ({ testimonials }) => {
    const [activeIndex, setActiveIndex] = useState(0);

    useEffect(() => {
        const intervalId = setInterval(() => {
            setActiveIndex((prevIndex) => (prevIndex + 1) % testimonials.length);
        }, 8000); // Change the interval as needed (in milliseconds)

        return () => {
            clearInterval(intervalId);
        };
    }, [testimonials]);

    return (
        <Box sx={{ flexGrow: 1 }}>
            <Paper elevation={7} sx={{ p: 2, textAlign: 'center', borderRadius: '10px',bgcolor: 'rgb(51, 72, 94)' }}>
                <Avatar alt={testimonials[activeIndex].name} src={testimonials[activeIndex].image} sx={{ width: 100, height: 100, margin: 'auto' }} />
                <Typography variant="h6" sx={{ mt: 2, fontFamily:'Oswald', fontWeight:'bold', color:'orange',fontSize:'25px'}}>{testimonials[activeIndex].name}</Typography>
                <Typography variant="subtitle1" sx={{fontWeight:'bold' ,color:'orange', fontSize:'15px' }}>{testimonials[activeIndex].position}</Typography>
                <Typography variant="body1" sx={{textAlign: 'start',fontFamily:'Oswald', color:'white',fontWeight:'normal',fontSize:'20px', padding:'20px'}}>"{testimonials[activeIndex].quote}"</Typography>
            </Paper>
        </Box>
    );
};

export default TestimonialContainer;
