import React, { useState } from 'react';
import { Container, TextField, Button, Grid, Typography, MenuItem,CircularProgress } from '@mui/material';
import ImageCropper from './ImageCropper';

function RegistrationForm() {
    const [formData, setFormData] = useState({
        name: '',
        position: '',
        profileImg: null,
        linkedIn: '',
        email: '',
        registrationNumber: '',
        phoneNumber: ''
    });

    const [croppedImage, setCroppedImage] = useState(null);
    const [cropperOpen, setCropperOpen] = useState(false);
    const [loading, setLoading] = useState(false);

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        setFormData({ ...formData, profileImg: file });

        setCropperOpen(true); // Open cropper when image is selected
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({ ...formData, [name]: value });

    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            if (croppedImage != null) {
                setLoading(true);
                console.log(croppedImage);
                // Create FormData object and append form fields
                const formDataToSend = new FormData();
                formDataToSend.append('name', formData.name);
                formDataToSend.append('registrationNumber', formData.registrationNumber);
                formDataToSend.append('phoneNumber', formData.phoneNumber);
                formDataToSend.append('email', formData.email);
                formDataToSend.append('post', formData.position);
                formDataToSend.append('linkedIn', formData.linkedIn);
                formDataToSend.append('authority', "Not Approved");

                // Append the cropped image Blob to FormData
                const fileExtension = croppedImage.type.split('/')[1]; // Extract file extension from blob's MIME type
                formDataToSend.append('fileExtension', fileExtension);
                formDataToSend.append('profileImg', croppedImage);

                // Send form data to server using fetch
                const serverResponse = await fetch('https://agamyatech.in/RegisterMember', {
                    method: 'POST',
                    body: formDataToSend,
                });

                if (!serverResponse.ok) {
                    throw new Error('Failed to submit form');
                }


                // Parse the response data as JSON
                const responseData = await serverResponse.json();

                // Check if the response contains any data you want to display
                if (responseData && responseData.status === "Success") {
                    // Inform the user about successful registration
                    alert(responseData.message);

                    // Reset form after successful submission
                    setFormData({
                        name: '',
                        position: '',
                        profileImg: null,
                        linkedIn: '',
                        email: '',
                        registrationNumber: '',
                        phoneNumber: ''
                    });
                } else {
                    // Inform the user about unsuccessful registration
                    alert(responseData.message);
                }
            } else {
                alert("Please Select Image");
                console.log("Please Crop Image");
            }

        } catch (error) {
            console.error('Error submitting form:', error);
        }finally {
            setLoading(false); // Set loading to false after form submission
        }
    };


    const onGet = (img) => {
        setCroppedImage(img);
        setCropperOpen(false); // Close cropper dialog
    }

    const onClose = () => {
        setCroppedImage(null);
        setCropperOpen(false); // Close cropper dialog
    }



    // Define the array of positions
    const positions = [
        "Web and Technical",
        "Treasure",
        "Designer",
        "Event Management",
        "PR & External Initiative",
        "Marketing",
        "Social Media",
        "Creative & Editor",
        "Product"
        // Add more positions as needed
    ];

    return (
        <Container maxWidth="sm">
            <form onSubmit={handleSubmit}>
                <Grid container spacing={2} justifyContent="center" alignItems="center">
                    <Grid item xs={12}>
                        <Typography variant="h4" align="center" gutterBottom style={{ fontFamily: "Oswald", fontWeight: 'bold', fontSize: '25px', color: 'orange' }}>Startup-Cell Registration Form</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="Name" name="name" value={formData.name} onChange={handleChange} required />
                    </Grid>

                    <Grid item xs={12}>
                        <TextField fullWidth label="Registration/Roll Number" name="registrationNumber" value={formData.registrationNumber} onChange={handleChange} required />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth type="tel" label="Phone Number" name="phoneNumber" value={formData.phoneNumber} onChange={handleChange} required />
                    </Grid>

                    <Grid item xs={12}>
                        <TextField fullWidth type="email" label="Email" name="email" value={formData.email} onChange={handleChange} required />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            fullWidth
                            select
                            label="Choose Field"
                            name="position"
                            value={formData.position}
                            onChange={handleChange}
                            required
                        >
                            {positions.map((position) => (
                                <MenuItem key={position} value={position}>
                                    {position}
                                </MenuItem>
                            ))}
                        </TextField>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField fullWidth label="LinkedIn" name="linkedIn" value={formData.linkedIn} onChange={handleChange} required />
                    </Grid>
                    <Grid item xs={12}>
                        <input type="file" accept="image/*" style={{ display: 'none' }} id="profile-img" name='profileImg' onChange={handleFileChange} />
                        <label htmlFor="profile-img">
                            <Button variant="contained" component="span">Choose Profile Image</Button>
                        </label>
                        {formData.profileImg && (
                            <ImageCropper
                                image={URL.createObjectURL(formData.profileImg)}
                                onSelect={onGet}
                                onCancel={onClose}
                                open={cropperOpen}
                            />
                        )}
                    </Grid>
                    <Button type="submit" variant="contained" fullWidth disabled={loading} style={{marginTop:'20px'}}>
                            {loading ? <CircularProgress size={24} color="secondary" /> : 'Submit Form'}
                        </Button>
                </Grid>
            </form>
        </Container>
    );
}

export default RegistrationForm;

