import React from 'react';
import { Container, Typography, Grid, Card, CardContent } from '@mui/material';
import Footer from '../components/Footer';

// Sample static initiative data
const initiativesData = [
    {
        id: 1,
        title: 'Initiative 1',
        description: 'Description of Initiative 1',
    },
    {
        id: 2,
        title: 'Initiative 2',
        description: 'Description of Initiative 2',
    },
    // Add more initiative data as needed
];

function InitiativePage() {
    return (
        <div style={{ width: '100%',  display: 'flex', flexDirection: 'column', minHeight: '100vh', background: 'linear-gradient(to bottom right, #1a2152, #1a4346)', paddingTop: '200px' }}>
            <Container maxWidth="lg">
                <Grid container spacing={3} justifyContent={'center'}>
                    {initiativesData.map((initiative) => (
                        <Grid item key={initiative.id} xs={12} sm={6} md={4}>
                            <Card style={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
                                <CardContent>
                                    <Typography variant="h5" component="h2" style={{ color: 'black' }}>{initiative.title}</Typography>
                                    <Typography variant="body1" color="text.secondary" style={{ color: 'black' }}>{initiative.description}</Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
            <Footer/>
        </div>
        
    );
}

export default InitiativePage;
