import { Container, Typography, Grid, Card, CardMedia, CardContent, Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';
import { makeStyles } from '@mui/styles';
import Footer from '../components/Footer';
// Sample static event data
const eventData = [
    {
        id: 1,
        name: 'Event 1',
        images: [
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
            '/images/nature1.jpg',
        ],
        description: 'Description of Event 1',
    },
    {
        id: 2,
        name: 'Event 2',
        images: [
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
            '/images/sky.jpg',
        ],
        description: 'Description of Event 2',
    },
    // Add more event data as needed
];


// Define custom CSS styles
const useStyles = makeStyles((theme) => ({
    HeadText: {
        background: '-webkit-linear-gradient(45deg, black 30%, white 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
        animation: '$gradientAnimation 3s linear infinite',
    },
    '@keyframes gradientAnimation': {
        '0%': {
            backgroundPosition: '0% 50%',
        },
        '50%': {
            backgroundPosition: '100% 50%',
        },
        '100%': {
            backgroundPosition: '0% 50%',
        },
    },
    nomalHeading: {
        background: '-webkit-linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        '-webkit-background-clip': 'text',
        '-webkit-text-fill-color': 'transparent',
    }
}));

function GalleryPage() {
    const [selectedEvent, setSelectedEvent] = useState(null);
    const [selectedImage, setSelectedImage] = useState(null);

    // Apply custom styles
    const classes = useStyles();

    const handleCardClick = (event) => {
        setSelectedEvent(event);
    };

    const handleImageClick = (image) => {
        setSelectedImage(image);
    };

    const handleCloseDialog = () => {
        setSelectedEvent(null);
    };

    const handleCloseImageDialog = () => {
        setSelectedImage(null);
    };

    return (
        <div style={{ width: '100%',  display: 'flex', flexDirection: 'column', minHeight: '100vh', paddingTop: '200px', background: 'linear-gradient(to right,purple, teal)' }}>
            <Container maxWidth="lg">
                <Grid marginTop={20} container spacing={3}>
                    {eventData.map((event) => (
                        <Grid item key={event.id} xs={12} md={6}>
                            <Card onClick={() => handleCardClick(event)}>
                                <CardMedia
                                    component="img"
                                    height="200"
                                    image={event.images[0]} // Displaying only the first image as a thumbnail
                                    alt={event.name}
                                />
                                <CardContent>
                                    <Typography className={classes.HeadText} style={{
                                        fontFamily: 'Oswald,sans-serif',
                                        fontWeight: 'bold'
                                    }} variant="h4" component="h2">{event.name}</Typography>
                                    <Typography className={classes.HeadText} style={{
                                        fontFamily: 'Oswald,sans-serif',
                                        fontWeight: 'bold'
                                    }} variant="body1" color="text.secondary">{event.description}</Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
                <Dialog open={Boolean(selectedEvent)} onClose={handleCloseDialog}>
                    <DialogTitle>{selectedEvent && selectedEvent.name}</DialogTitle>
                    <DialogContent>
                        <Grid container spacing={2}>
                            {selectedEvent && selectedEvent.images.map((image, index) => (
                                <Grid item key={index} xs={6} sm={4} md={3}>
                                    <Card onClick={() => handleImageClick(image)}>
                                        <CardMedia
                                            component="img"
                                            height="100"
                                            image={image}
                                            alt={`Event ${index + 1}`}
                                        />
                                    </Card>
                                </Grid>
                            ))}
                        </Grid>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseDialog}>Close</Button>
                    </DialogActions>
                </Dialog>
                <Dialog open={Boolean(selectedImage)} onClose={handleCloseImageDialog} maxWidth="xl">
                    <DialogContent>
                        <img src={selectedImage} alt="" style={{ width: '100%', height: 'auto' }} />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseImageDialog}>Close</Button>
                    </DialogActions>
                </Dialog>
            </Container>
            <Footer/>
        </div>
    );
}

export default GalleryPage;
